import { Server } from "miragejs"
import partial from './data/partial'
import full from './data/full'

export const makeServer = ({ environment = "production" } = {}) => {
  let server = new Server({
    environment,

    routes() {
      this.get("/full", () => JSON.stringify(full));
      this.get("/partial", () => JSON.stringify(partial));
    }
  })
};
