import React, {useState, Suspense, lazy} from "react";
import {Button, Container, Col, Spinner} from "reactstrap";

const WelcomeScreen = () => {
  const [isTableRendered, renderTable] = useState('');
  const buttonData = [
    {
      name: 'Full',
      color: 'danger',
      key: 'Full',
      onClick: () => renderTable('full'),
    },
    {
      name: 'Partial',
      color: 'success',
      key: 'Partial',
      onClick: () => renderTable('partial'),
    }
  ];
  const DataTable = lazy(() => import('./DataTable'));

  return (
    <Container style={{minHeight: '100vh'}} className='d-flex justify-content-center align-items-center'>
      {isTableRendered ?
          <Suspense fallback={<Spinner color={'primary'}/>}>
            <Col xs='12' className='mb-2 mt-4'>
              <DataTable tableType={isTableRendered}/>
            </Col>
          </Suspense>
        :
        <Col className={'d-flex justify-content-center'}>
          {buttonData.map(data => <Button {...data}>{data.name}</Button>)}
        </Col>}
    </Container>
  )
};

export default WelcomeScreen;
